import React, {Fragment, useState, useEffect} from 'react';
import axios from 'axios';

import { Modal } from '@consta/uikit/Modal';
import { Grid, GridItem } from '@consta/uikit/Grid';
import { Button } from '@consta/uikit/Button';
import { Checkbox } from '@consta/uikit/Checkbox';
import { Text } from '@consta/uikit/Text';
import { TextField } from '@consta/uikit/TextField';
import { Card } from '@consta/uikit/Card';
import { DatePicker } from '@consta/uikit/DatePickerCanary';
import { Select } from '@consta/uikit/Select';

export const Calculate = () => {
    const [content, setContent] = useState([]);
    const [isModalOpen, setIsModalOpen] = React.useState(false);
    const [isModalCalendarOpen, setIsModalCalendarOpen] = React.useState(true);
    const [contentModal, setContentModal] = useState({'status_code':'null', 'id':'null'})
    const [values, setValues] = useState([]);
//    const [value, setValue] = useState<string | null>(null);

    const [reqdata, setReqdata] = useState({
            'withVariablesInReturn': true,
            'variables': {

                'serviceName': {
                    'value': '',
                    'type': 'string',
                },
                'numberOfDays': {
                    'value': '',
                    'type': 'string',
                },
                'finalPrice': {
                    'value': '',
                    'type': 'string',
                }
            }
    })

    const handleChange = (event) => {
        setValues({...values, [event.name]: event.value})
        console.log('ID: ', event, ' value: ', event.value)
    }

//    const handleChange = ({ value }: { value: string | null }) => setValue(value);

    const handleClick = (event) => {
        event.preventDefault();
        console.log('Data: ', reqdata)
        if (event.target.textContent=='Отменить') {

        }
        else {
            console.log(JSON.stringify(reqdata))
            axios({
                    method: "POST",
                    url: window.location.origin + '/calculateservice',
                    headers: { Authorization : localStorage.getItem('access_token'),
                               Group : localStorage.getItem('detailRole'),
                               'Content-Type' : 'application/json', },
                    data: JSON.stringify(reqdata),
            }).then(response =>
                {
                    setContentModal(JSON.parse(response.data))
                }
            ).catch(error => {console.log('Error View: ' + error)})
        }
    }

    const divStyle = {
        marginLeft: '0px',
        marginTop: '5px',
        marginBottom: '5px',
    };

    const divStyleLeft = {
        marginTop: '10px',
        marginBottom: '5px',
    };

    const divStyleRight = {
        marginTop: '10px',
        marginBottom: '5px',
        marginLeft: '10px',
    };

    useEffect( () => {
        var str = ""
        for (let key in values) {
            str = str + key + " " + values[key] + "  \n"
        }
        setReqdata({...reqdata, 'variables':{...reqdata.variables, 'serviceDescription':{...reqdata.variables.serviceDescription, 'value':str}}})
    }, [values])

    return(
        <Fragment>
            <Grid cols='12' gap='xs' xAlign='left' breakpoint={{
                xs: { colgap: 'xs', },
                m: { colgap: 'm', },
            }}
            >
                <GridItem col='12' row='3'/>
                <GridItem col='1'/>
                <GridItem col='8'>
                {/*
                    <div>
                        <Text view='brand' font='serif' size='xl' weight='bold' lineHeight='l' align='left'>
                            Расчет стоимости услуг
                        </Text>
                    </div>
                */
                }
                    <TextField id='serviceName' style={divStyle} width='full' size='m' onChange={handleChange} value={values['serviceName']} type='text' placeholder='Расчет стоимости услуг'/>
                </GridItem>

                <GridItem col='3'/>
                <GridItem col='12' row='3'/>
                <GridItem col='1' row='4'/>
                <GridItem col='4' row='4'>
                    <Card verticalSpace='xl' horizontalSpace='xl'>
                        <form>
                            <Grid col='12'>
                                <GridItem col='3'>
                                    <Text as='p' view='secondary' font='serif' size='m' weight='bold' lineHeight='s' align='left'>
                                        Услуга
                                    </Text>
                                </GridItem>
                            </Grid>
                           { //<Button style={divStyleLeft} label='Расчитать' form='default' size='m' view='primary' onClick = {handleOnClick}/>
                           }
                        </form>
                    </Card>
                </GridItem>
            </Grid>
        </Fragment>

    )
}